import React, {Component} from 'react';
import uuid from 'uuid';
import '../css/About.css';
import Navi from './Navi';
import Contact from './Contact';
import {Link} from 'react-router-dom';
function Info_1() {
  return(
      <div className="info">
        <div className="left">
          HI
        </div>
        <div className="right">
          <div className="talk">
            <p>안녕하세요.</p>
            <p>제 포트폴리오 사이트를 방문해주셔서 감사합니다.</p>
            <p>저는 개발자 김두리입니다.</p>
            <p>이 사이트가 마음에 드셨으면 좋겠습니다.</p>
          </div>
        </div>
      </div>
  );
}
function Info_2() {
  return(
      <div className="info">
        <div className="left fadein">
          I'M
        </div>
        <div className="right">
          <div className="talk">
            <p className="fadein">저는 <span className ="underbar">풀스택</span> 개발자를 지향하고 있으며,</p>
            <p className="fadein">백엔드는 <span className="underbar">Express</span>, 프론트엔드는 <span className="underbar">React.js</span>를 활용합니다.</p>
            <p className="fadein">언제나 새로운 방법을 생각하며, 문제를 해결해나갑니다.</p>
            <p className="fadein">코딩을 즐기고, 다른 사람에게 서비스를 제공하는 것에 기쁨을 느낍니다.</p>
            <p className="fadein">새로운 언어와 기술을 배우는 것을 즐기며, 다양한 경험을 하는 것을 좋아합니다.</p>
          </div>
      </div>
      </div>
  );
}
class About extends Component {
_isMounted = false;
  constructor() {
    super();
    this.state={
      isTimeout:false,
      isScrolled:false,
      isChanged:false,
      content:'1'
    }
  }
  shouldComponentUpdate(nextProps, nextState) {
    if(this.state.isScrolled && nextState.content === this.state.content) {
      return false;
    }
    return true;
  }
onWheel = (e) => {
  if(this._isMounted){
    if(e.deltaY >= 100) {
      if(document.getElementsByClassName('info')[0]) {
      document.querySelector('.scr').classList.add('hide');
        if(this.state.isScrolled && !this.state.isChanged && this.state.content==1) {
          document.querySelector('.info .left').classList.remove('fadein');
          document.querySelector('.info .left').classList.add('fadeout');
          for(var i in document.querySelectorAll('.talk p')) {
            document.querySelectorAll('.talk p')[i].classList.remove('fadein');
            document.querySelectorAll('.talk p')[i].classList.add('fadeout');
            if(i>=document.querySelectorAll('.talk p').length -1) break;
          }
          setTimeout(()=>{this.setState({content:2})},400);
          setTimeout(()=>{this.setState({isChanged:true})},3000);
        }
        if(this.state.isChanged) {
          this.setState({content:3});
        }
      }
    }
  }
}
onScroll = (e) => {
  if(this._isMounted){
    if(document.getElementsByClassName('info')[0]) {
      var scrollY = e.srcElement.scrollingElement.scrollTop;
        if(scrollY > document.querySelector('.about_cvs').offsetHeight-1) {
          document.querySelector('.about_cvs').classList.add('fix');
          document.querySelector('.about_cvs .right h1:first-child').classList.add('visible');
          document.querySelector('.about_cvs .right h1:last-child').classList.add('visible');
          document.querySelector('.info .left').classList.add('fadein');
          if(this.state.content == 1)
            setTimeout(()=>{for(var i in document.querySelectorAll('.talk p')) {
              document.querySelectorAll('.talk p')[i].classList.add('fadein');
              if(i>=document.querySelectorAll('.talk p').length -1) break;
            }},400);
          if(this.state.content < 3)
            setTimeout(()=>{
              this.setState({isScrolled:true});
                document.querySelector('.scr').classList.remove('hide');
            },3000);
      }
    }
  }
}
componentWillUnmount() {
  this._isMounted = false;
}
  componentDidMount() {
    this._isMounted = true;
    setTimeout(function() {this.setState({isTimeout:true}); }.bind(this),3000);
    window.addEventListener('scroll',this.onScroll);
    window.addEventListener('wheel',this.onWheel);
  }
  render() {

    return(
      <div className="after">
        <Navi/>
        {this.state.isTimeout?<a className="scr"><span></span><span></span><span></span></a>:''}
        <div className="about_cvs">
          <div className="left">
            <h1>{this.state.isScrolled?'WORKA':'WORK'}</h1>
          <h1>{this.state.isScrolled?'SIMPLE &':'SIMPLE  '}</h1>
          <h1 onMouseOver={
            function() {
              if(this.state.isScrolled) {
                document.querySelector('.about_cvs .left h1:last-child').classList.add('hover');
                document.querySelector('.about_cvs .right h1:last-child').classList.add('hover');
              }
            }.bind(this)
          } onMouseOut={
            function() {
              if(this.state.isScrolled) {
                document.querySelector('.about_cvs .left h1:last-child').classList.remove('hover');
                document.querySelector('.about_cvs .right h1:last-child').classList.remove('hover');
              }
            }.bind(this)
          }><Link to="/contact">THINK</Link></h1>
          </div>
          <div className="right">
            <div className="text">
              <h1>HOLIC</h1>
              <h1>CREATIVE</h1>
              <h1 onMouseOver={
                function() {
                  if(this.state.isScrolled) {
                    document.querySelector('.about_cvs .left h1:last-child').classList.add('hover');
                    document.querySelector('.about_cvs .right h1:last-child').classList.add('hover');
                  }
                }.bind(this)
              } onMouseOut={
                function() {
                  if(this.state.isScrolled) {
                    document.querySelector('.about_cvs .left h1:last-child').classList.remove('hover');
                    document.querySelector('.about_cvs .right h1:last-child').classList.remove('hover');
                  }
                }.bind(this)
              }><Link to="/contact">ME</Link></h1>
            </div>
          </div>
        </div>
        {this.state.isTimeout? this.state.content <= '1'?<Info_1/>:<Info_2/>:''}
        {this.state.content=='3'?<div className="cvs_space"/>:''}
      </div>
    );
  }
}
export default About;
